
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import tabBar from '../../components/TabBar';
import tools from '../../lib/tools.js';

export default {
name:'personal',
components:{Flexbox, FlexboxItem,Toast,tabBar},
data(){
return {
id:'',
info:{},
toastMsg:'',
showToast:false,
imgurl:''
}
},
mounted(){

var vm = this;
let id = (tools.getRequestParam('md') || localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id'));//md === userid

if(!id || id=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}// 此时可以跳转到登陆，等登陆后在跳转回来
this.id = id;
this.$http.post('/apiUser/queryAccount?managerId='+id,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
vm.info = data.data;
vm.imgurl = 'url('+vm.info.imageUrl+')';
}else{
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
methods:{
goToFamilyDoctor(){
let vm = this;

this.$http.get('/api/contracte/queryDoctors?managerId='+this.id).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data && data.data.length>0){
tools.next('/m/myFamilyDoctors.html');
}else{
tools.next('/m/familyOrg.html');
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
toggleAccount(){
tools.next("/m/toggleAccount.html?md="+this.id);// md===userid
},
goto(url){
tools.next(url);
}
}
}



